import { useReducer } from "react";
const reducer = (state, action) => {
  if (action.type === "decrement") {
    return { counter: state.counter - 1 };
  } else if (action.type === "increment") {
    return { counter: state.counter + 1 };
  } else if (action.type === "makeitzero") {
    return { counter: 0 };
  } else if (action.type === "increaseby50") {
    return { counter: state.counter + 50 };
  } else if (action.type === "increaseby100") {
    return { counter: state.counter + 100 };
  } else {
    throw new Error();
  }
};

const initialstate = { counter: 0 };

function Counter() {
  const [state, dispatch] = useReducer(reducer, initialstate);
  return (
    <div>
      <h1>Count:{state.counter}</h1>
      <button onClick={() => dispatch({ type: "decrement" })}>-</button>
      <button onClick={() => dispatch({ type: "increment" })}>+</button>
      <button onClick={() => dispatch({ type: "makeitzero" })}>reset</button>
      <br />
      <br />
      <button onClick={() => dispatch({ type: "increaseby50" })}>
        Increase by 50{" "}
      </button>
      <button onClick={() => dispatch({ type: "increaseby100" })}>
        Increase by 100
      </button>
    </div>
  );
}
export default Counter;
